* {
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
      'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
  }
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  .search-form {
    display: flex;
    justify-content: center;
    padding: 8px;
    margin-bottom: 24px;
  }
  .button {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  
  .button.is-hidden {
    display: none;
  }
  
  /* Hits List */
  .gallery {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 20px;
    padding: 0;
    margin: 0;
    list-style: none;
    max-width: calc(100vw - 80px);
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
  }
  
  .gallery li {
    height: 450px;
    overflow: hidden;
  }
  
  .gallery img {
    display: block;
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .gallery a {
    text-decoration: none;
    color: #2a2a2a;
  }